.image-block {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  width: 100%;
  
  margin-right: 0;
  margin-bottom: var(--size-5);

  @media (min-width: 1024px) {
    margin-right: var(--size-5);
    margin-bottom: 0;
  }

  img {
    max-width: 100%;
    border-radius: var(--size-1) var(--size-1) 0px 0px;
  }

  .deadline {
    background-color: var(--color-blue-500);
    position: absolute;
    bottom: 0;
    right:0;
    left:0;
    color: var(--color-white);
    padding: var(--size-2);
    font-size: var( --size-2_5);
    border-radius: 0px 0px var(--size-1) var(--size-1);
    
  }

  .label-container {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    text-align: left;
    padding: 8px 0 0 8px;
  }
}
