
.ListingBlock{
  
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: left;
    width: 100%;
    
    margin-right: 0;
    margin-bottom: var(--size-5);
  
    @media (min-width: 1024px) {
      margin-right: var(--size-5);
      margin-bottom: 0;
    }
    .listingLabel-container {
        display: flex;
        justify-content: space-between;
       
        color: var(--color-white);
        
        font-size: var( --size-2_5);
        border-radius: 0px 0px var(--size-1) var(--size-1);
        
      }
    .name{
      font-family: 'Lato';
      font-style: normal;
      font-weight: 600;
      font-size: 32px;
      padding: 1rem 1rem 0.5rem 0px;
      color: #0077DA;
     
    }
    .address{
      padding: 1rem 1rem 0 0px;
      margin-bottom: 0.5rem;
    }
}